import React, { Component } from 'react';
import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler,
} from 'chart.js';
import zoomPlugin from 'chartjs-plugin-zoom';
import config from '../config';
import 'bootstrap/dist/css/bootstrap.min.css';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler,
  zoomPlugin
);

export class Home extends Component {
  static displayName = Home.name;

  state = {
    waterLevelData: [],
    rainfallData: [],
    tubes: [], 
    selectedTube: '', 
    forecast: {
      description: '',
      temperature: '',
      rain: '',
    },
    currentWaterLevel: null,
    lastMeasurementDate: null
  };

  getCurrentDate() {
    const today = new Date();
    return today.toISOString().split('T')[0];
  }

  calculateStartDate() {
    const today = new Date();
    const threeMonthsAgo = new Date(today.setMonth(today.getMonth() - 3));
    return threeMonthsAgo.toISOString().split('T')[0];
  }

  componentDidMount() {
    this.fetchTubes();
    this.fetchWeatherForecast(); 
  }

  async fetchTubes() {
    try {
      const response = await fetch(`${config.apiBaseUrl}/waterpeil/getTubes`);
      if (!response.ok) {
        throw new Error('Error fetching tubes');
      }
      const tubes = await response.json();
      this.setState({ tubes });
    } catch (error) {
      console.error('Error fetching tubes:', error);
    }
  }
  

  async fetchWeatherForecast() {
    try {
      const response = await fetch(`${config.apiBaseUrl}/weather/getForecastForTomorrow`);
      if (!response.ok) {
        throw new Error('Error fetching weather forecast');
      }

      const forecast = await response.json();
      this.setState({
        forecast: {
          description: forecast.description,
          temperature: `${forecast.minTemp}C / ${forecast.maxTemp}C`,
          rain: forecast.rainChance,
        },
      });
    } catch (error) {
      console.error('Error fetching forecast for tomorrow:', error);
    }
  }

  handleTubeSelect = async (event) => {
    const selectedTube = event.target.value;
    this.setState({ selectedTube });

    if (selectedTube) {
      try {
        const startDate = this.calculateStartDate();
        const endDate = this.getCurrentDate();

        const waterResponse = await fetch(`${config.apiBaseUrl}/waterpeil/getWaterpeilByTube/${selectedTube}?startDate=${startDate}&endDate=${endDate}`);
        if (!waterResponse.ok) {
          throw new Error('Error fetching water data');
        }

        const weatherResponse = await fetch(`${config.apiBaseUrl}/weather/getWeatherHistory`);
        if (!weatherResponse.ok) {
          throw new Error('Error fetching weather data');
        }

        const waterData = await waterResponse.json();
        const weatherData = await weatherResponse.json();
        const waterLevelData = waterData.map(d => ({
          date: new Date(d.datum).toLocaleDateString('nl-NL'), 
          level: d.waterpeil.toFixed(2)  
        }));

        const rainfallData = weatherData.map(d => ({
          date: new Date(d.datum).toLocaleDateString('nl-NL'), 
          amount: d.totaalRegen || 0 
        }));

        const lastIndex = waterData.length - 1;
        const lastMeasurementDate = new Date(waterData[lastIndex]?.datum).toLocaleDateString('nl-NL') || null;
        const currentWaterLevel = waterData[lastIndex]?.waterpeil.toFixed(2) || null;

        this.setState({ waterLevelData, rainfallData, lastMeasurementDate, currentWaterLevel });
      } catch (error) {
        console.error('Error fetching water level and weather data:', error);
      }
    }
  };

  render() {
    const { waterLevelData, rainfallData, forecast, currentWaterLevel, tubes, selectedTube, lastMeasurementDate } = this.state;

    const chartData = {
      labels: waterLevelData.map((data) => data.date),
      datasets: [
        {
          label: 'Waterpeil (m)',
          data: waterLevelData.map((data) => data.level),
          borderColor: 'red',
          fill: false,  
          tension: 0.4,
          yAxisID: 'y-axis-water',
        },
        {
          label: 'Regenval (mm)',
          data: rainfallData.map((data) => data.amount),
          borderColor: 'blue', 
          backgroundColor: 'rgba(66, 135, 245, 0.4)', 
          fill: true, 
          tension: 0.4,
          yAxisID: 'y-axis-rainfall',
        },
      ],
    };

    const chartOptions = {
      scales: {
        'y-axis-water': {
          type: 'linear',
          position: 'left',
          ticks: {
            beginAtZero: true,
          },
          title: {
            display: true,
            text: 'Waterpeil (m)',
          },
        },
        'y-axis-rainfall': {
          type: 'linear',
          position: 'right',
          ticks: {
            beginAtZero: true,
          },
          title: {
            display: true,
            text: 'Regenval (mm)',
          },
        },
      },
      plugins: {
        zoom: {
          pan: {
            enabled: true,
            mode: 'x',
          },
          zoom: {
            wheel: {
              enabled: true,
            },
            pinch: {
              enabled: true,
            },
            mode: 'x',
          },
        },
      },
      responsive: true,
      maintainAspectRatio: false,
    };

    return (
      <div className="container mt-5">
        <h1 className="text-center mb-5">Waterpeil en Regenval</h1>

        <div className="row mb-4">
          <div className="col-md-4">
            <label>Selecteer een Tube</label>
            <select className="form-control" value={selectedTube} onChange={this.handleTubeSelect}>
            <option value="">-- Selecteer een Tube --</option>
            {tubes.map((tube) => (
              <option key={tube.tubeNumber} value={tube.tubeNumber}>
                {tube.tubeName} ({tube.tubeNumber})
              </option>
            ))}
          </select>
          </div>
        </div>

        <div className="row">
          <div className="col-md-8 mb-4">
            <div className="card shadow-sm p-4">
              <h2 className="card-title text-center mb-4">Waterpeil en Regenval Overzicht</h2>
              <div style={{ height: '500px' }}>
                <Line data={chartData} options={chartOptions} />
              </div>
            </div>
          </div>

          <div className="col-md-4">
            <div className="card shadow-sm p-4 mb-4">
              <h2 className="card-title text-center mb-4">Weersvoorspelling voor Morgen</h2>
              {forecast ? (
                <div className="text-center">
                  <p className="mb-2">Verwachting: {forecast.description}</p>
                  <p className="mb-1">
                    Temperatuur: <strong>{forecast.temperature}</strong>
                  </p>
                  <p>
                    Neerslagkans: <strong>{forecast.rain}%</strong>
                  </p>
                </div>
              ) : (
                <p className="text-center">Weersvoorspelling laden...</p>
              )}
            </div>

            <div className="card shadow-sm p-4">
              <h2 className="card-title text-center mb-4">Huidige Waterstand</h2>
              {currentWaterLevel !== null ? (
                <p className="text-center">
                  De huidige waterstand is <strong>{currentWaterLevel} meter</strong>.<br />
                  Laatste meting op <strong>{lastMeasurementDate}</strong>.
                </p>
              ) : (
                <p className="text-center">Huidige waterstand laden...</p>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
