import { Home } from "./components/Home";
import { ManageDevicesAndAPIs } from "./components/ManageDevicesAndAPIs";
import { ToggleDevices } from "./components/ToggleDevices";

const AppRoutes = [
  {
    index: true,
    element: <Home />
  },
  {
    path: '/device-management',
    element: <ManageDevicesAndAPIs />
  },
  {
    path: '/toggle-devices',
    element: <ToggleDevices />
  }
];

export default AppRoutes;
